}
.swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-left-enter-active, .swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-left-leave-active, .swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-right-enter-active, .swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-right-leave-active {
        -webkit-transition: all .5s ease;
        transition: all .5s ease;
}
.swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-left-enter {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
}
.swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-left-leave-to {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
}
.swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-right-enter {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
}
.swiper .swiper-wrapper .swiper-list .swiper-item .swiper-img.swipe-right-leave-to {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
}
